import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../components/Main.vue'
import Datastructure from '../components/datastructure.vue'
import Python from '../components/python.vue'

Vue.use(VueRouter)

const routes = [
  // 重定向
  {
    path: '/',
    meta: {
      title: '仇志金 Zhijin Qiu'
    },
    redirect: '/main'
  },
  {
    path: '/main',
    meta: {
      title: '仇志金 Zhijin Qiu'
    },
    component: Main
  },
  {
    path: '/datastructure',
    meta: {
      title: '数据结构 仇志金 Zhijin Qiu'
    },
    component: Datastructure
  },
  {
    path: '/python',
    meta: {
      title: 'Python 仇志金 Zhijin Qiu'
    },
    component: Python
  }
]

const router = new VueRouter({
  routes
})

